/*
container of the register component
set his width to be 80% of the screen size
add a thin border so the space occupied by this component becomes visible
and some margins to bring it to the middle of the screen
*/

h5 {
    text-align: center;
    text-transform: uppercase;
    margin: 20px 0; /*this works for top and bottom only the other combination works for left and right only*/
    color: red;
}

figure {
    text-align: center;
    margin: 0;
    padding: 0;
}

figcaption {
    margin-top: 8px;
    color: darkslategray;
    font-size: 0.9em;
}

img {
    border: 10px dashed darkslategray;
    caption-side: bottom;
    margin: 10px;
}

#container {
    border: 2px solid darkgray;
    display: block;
    width: 70%;
    padding: 15px;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
}

#username, #password, #preview_profile_picture {
    margin: 2px;
}

#preview_profile_picture {
    width: 315px;
    height: 315px;
    object-fit: cover;
    display: block;
    margin: 0 auto;
}
